.navbar {
  margin: 20px;
}

.bottom-border {
  height: 1px;
  border-bottom: 1px solid gray;
  position: absolute;
  z-index: 1;
  bottom: 0px;
  -webkit-transform: translateY(-60px);
          transform: translateY(-60px);
  transition: 0.5s ease-in-out;
  -webkit-transition: 0.5s ease-in-out;
}

.bottom-border-second {
  height: 1px;
  width: 100%;
  border-bottom: 1px solid rgb(189, 189, 189);
  position: relative;
  z-index: -1;
  top: 53px;
  -webkit-transform: translateY(-60px);
          transform: translateY(-60px);
  transition: 0.5s linear;
  -webkit-transition: 0.5s linear;
}

.nav-items ul {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  list-style-type: none;
}

.navbar a {
  position: relative;
  text-decoration: none;
  color: gray;
  font-weight: 100;
  font-size: 0.9rem;
  transition: 0.5s ease;
  -webkit-transition: 0.5s ease;
}

.navbar a:hover {
  color: rgb(27, 169, 204);
}

.navbar a:before {
  width: 30px;
  height: 30px;
  content: "";
  border: 1px solid black;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  transform: scale(0.001, 0.001);
  -webkit-transform: scale(0.001, 0.001);
}

.navbar a:focus {
  outline: 0;
  color: black;
  font-weight: 400;
}

.navbar a:focus:before {
  animation: rounded_circle 0.4s ease-out;
  -webkit-animation: rounded_circle 0.4s ease-out;
}

@-webkit-keyframes rounded_circle {
  50% {
    transform: scale(2, 2);
    -webkit-transform: scale(2, 2);
    opacity: 0;
  }
  80% {
    transform: scale(0.001, 0.001);
    -webkit-transform: scale(0.001, 0.001);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
    opacity: 2;
  }
}

@keyframes rounded_circle {
  50% {
    transform: scale(2, 2);
    -webkit-transform: scale(2, 2);
    opacity: 0;
  }
  80% {
    transform: scale(0.001, 0.001);
    -webkit-transform: scale(0.001, 0.001);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
    opacity: 2;
  }
}
